import React from 'react'
import { Card, WingBlank, WhiteSpace } from 'antd-mobile';

export default class CcmyCard extends React.Component{
    render(){
        return(
            <WingBlank size="lg">
            <WhiteSpace size="lg" />
            <Card>
              <Card.Header
                title=""
                thumb={require('../images/LOGO.png')}
                extra={<span>:</span>}
              />
              <Card.Body>
                <div>
                  <h3>:</h3>
                  <p style={{textIndent: 30}}>,,,,,,,,,,,,,,,,,,</p>
                  <h3>:</h3>
                  <p style={{textIndent: 30}}></p>
                  <h3>:</h3>
                  <p style={{textIndent: 30}}>15518535588</p>
                  <p style={{textIndent: 30}}>18639307788 </p>
                  <img style={{width: '100%'}} src={require('../images/location.png')}/>
                </div>
              </Card.Body>
              <Card.Footer content="" extra={<div></div>} />
            </Card>
            <WhiteSpace size="lg" />
          </WingBlank>
        )
    }
}


